export const playing = {
    data() {
        return {
            playIndex: .5,
            playName: null,
            playId:null
        }
    },
    mounted() {
        /**playing
 * @param1 
 * @param2 
 */
        this.$bus.$on('Playing', (index, name,id) => {
            this.playIndex = index;
            this.playName = name;
            this.playId=id;
        })
    },
}