<template>
  <div class="player-lyric" :style="lyricStyle" @mouseenter="handleRefresh">
    <scroll
      :disable-bar="disableBar"
      :height="height"
      disable-wheel
      @moveStart="isDarg = true"
      @moveEnd="isDarg = false"
      ref="scroll"
    >
      <ul>
        <li
          v-for="(line, index) in lyricArray"
          :key="index"
          :class="[
            index == lyricIndex ? `${'player-action-' + theme}` : '',
            middle ? 'player-action-middle' : '',
          ]"
        >
          {{ line[1] }}
        </li>
      </ul>
    </scroll>
  </div>
</template>
<script>
import {forcible} from "mixin/components/forcible-refresh"
import { theme } from "mixin/global/theme";
import { parseLyric } from "utils/parse-lyric";
import Scroll from "common/scroll/Scroll";
import { lyricItem } from "./init-songs";
export default {
  name: "Lyric",
  mixins: [theme,forcible],
  components: { Scroll },
  props: {
    disableBar: {
      type: Boolean,
      default: false,
    },
    lyric: {
      type: String,
      default() {
        return "";
      },
    },
    height: {
      type: String,
      default: "",
    },
    currentTime: {
      type: Number,
      default: 0,
    },
    /**
     * 
     * true
     */
    middle: {
      type: Boolean,
      default: false,
    },
    /**middletrue
     * 
     * 
     */
    scrollNum: {
      type: Number,
      default: 5,
    },
  },
  data() {
    return {
      lyricArray: [], //
      lyricIndex: -1, //
      midIndex: 0,
      length: 0,
      isDarg: false, //truescrollTo
    };
  },
  watch: {
    lyric() {
      this.lyricIndex = -1;
      this.midIndex = 0;
      this.$refs.scroll.scrollTo(0, 0, true);
      /** */
      this.lyricArray = parseLyric(this.lyric);
      /** */
      this.$nextTick(() => {
        this.refresh();
        /** */
        this.$refs.scroll.scrollTo(0, 0);
      });
    },
    /** */
    currentTime(currentTime) {
      /** */
      for (let i = 0; i < this.lyricArray.length; i++) {
        const line = this.lyricArray[i];
        const nextLine = this.lyricArray[i + 1];
        /**currentTimecurrentTime */
        if (
          currentTime >= line[0] &&
          (!nextLine || currentTime < nextLine[0])
        ) {
          this.lyricIndex = i;
          /** */
          if (this.middle) {
            /**list */
            if (
              this.lyricIndex < this.scrollNum ||
              this.lyricIndex > this.lyricArray.length - this.scrollNum
            )
              return;

            /** */
            if (!this.isDarg) {
              this.$refs.scroll.scrollTo(
                -30 * (this.lyricIndex - this.scrollNum),
                2,
                false
              );
            }
          } else {
            this.$refs.scroll.scrollTo(-30 * this.lyricIndex, 0, false);
          }
        }
      }
    },
  },
  methods: {
    /**scroll */
    refresh() {
      this.$refs.scroll.refresh();
    },
  },
  computed: {
    lyricStyle() {
      return {
        height: this.middle
          ? `${(this.scrollNum * 2 + 1) * 30}px`
          : this.height,
      };
    },
  },
};
</script>
<style scoped>
.player-lyric {
  padding: 0px 20px;
  overflow: hidden;
}
.player-lyric ul {
  text-align: center;
  list-style-type: none;
}
.player-lyric ul li {
  height: 30px;
  line-height: 30px;
  padding: 5px 0px;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.player-action-light {
  font-size: 28px;
  color: var(--primary);
}
.player-action-dark {
  font-size: 28px;
  color: #b82525;
}
.player-action-green {
  font-size: 28px;
  color: var(--green-main-color);
}
/* list middletrue-- */
.player-action-middle {
  font-size: 13px;
  text-align: left;
}
</style>